.DialogsWrapper {
    display: grid;
    grid-template-columns: 150px 1fr;
}

.Users {
    text-align: center;
    padding-top: 10px;
    background-color: rgb(255, 213, 128);
    border-right: #ffffff solid 2px;
}

.Users .User {
    display: grid;
    grid-template-columns: 40% 60%;
    padding-top: 10px;
    padding-right: 15px;
    padding-left: 15px;
}

.User .ImgEl {
    grid-column-start: 1;
}

.User .NameEl {
    grid-column-start: 2;
}

.Users .User a {
    text-decoration: none;
    vertical-align: middle;
    color: black;
}

.Users .User img {
    width: 30px;
    margin: 2px;
    vertical-align: middle;
    background-color: white;
    border: white solid 1px;
    border-radius: 30px;
}

.Users .User.Active {
    color: #ffffff;
}

.Messages {
    padding-top: 25px;
    padding-left: 30px;
}

.Messages .Message {
    padding-bottom: 10px;
}